import {MergeFields} from './../../operator/mergefields/index.js';
import {basicSetup, EditorView} from "codemirror";
import {materialDark} from "@ddietr/codemirror-themes/material-dark.js";
import {twig} from "./languages/twig/language.js";

(function ( $, window, document, undefined ) {
    /**
     * https://codemirror.net/docs/migration/#codemirror.fromtextarea
     *
     * @returns {EditorView}
     */
    function editorFromTextArea(textarea) {
        let view = new EditorView({
            doc: textarea.value,
            extensions: [
                basicSetup,
                materialDark,
                twig(),
                EditorView.updateListener.of((v) => {
                    if (v.docChanged) {
                        $(textarea).trigger('codemirror:changed', view.state.doc.toString())
                    }
                })
            ],
        })
        textarea.parentNode.insertBefore(view.dom, textarea)
        textarea.style.display = "none"
        if (textarea.form) {
            textarea.form.addEventListener("submit", () => {
                textarea.value = view.state.doc.toString()
            })
        }

        return view
    }

    /**
     * Toolbar helpers.
     *
     * @param $container
     * @constructor
     */
    function Toolbar($container)
    {
        "use strict";

        /**
         * Container.
         *
         * @type {jQuery}
         */
        var $toolbar;

        /**
         * Create the toolbar.
         *
         * @return {void}
         */
        var createToolbar = function () {
            $toolbar = $('<div>', {class: 'toolbar-wrapper sp-z-50'})
                .append(
                    $('<div>', {class: 'toolbar'})
                );

            $container.prepend($toolbar);
        }

        /**
         * Add a button to the toolbar. Use .on('click', fn) to handle what should happen when the
         * toolbar button is pressed.
         *
         * @param name
         * @param data
         * @returns {jQuery}
         */
        this.addButton = function (name, data) {
            if (! $toolbar) {
                createToolbar();
            }

            var $button = $('<a/>', {
                class: 'button button-icon button-name-' + name,
                href: '#',
                title: data.title || '',
                role: 'button',
                alt: data.title || ''
            });
            if (data.icon) {
                $button.html(data.icon);
            }

            $toolbar.find('.toolbar').append($button);

            return $button;
        };
    }

    /**
     * Add merge fields to the toolbar.
     *
     * @param {{codemirror: Editor, syntaxEmailTemplate: boolean}} parameters
     */
    function addToToolbar(parameters)
    {
        "use strict";

        var codemirror = parameters.codemirror;
        var mergeFields = new MergeFields({
            valFn: () => codemirror.codemirror().state.doc.toString(),
            syntaxEmailTemplate: parameters.mergefields_syntaxEmailTemplate
        });

        mergeFields.init(codemirror.container());
        mergeFields.callback(codemirror.codemirror().state.doc.toString());
        codemirror.element().on('codemirror:changed', function (e, val) {
            mergeFields.callback(val);
        });

        var $button = parameters.toolbar.addButton('mergefields', {
            title: MergeFields.translations.merge_fields,
            icon: MergeFields.icon
        });

        $button.on('click', function (e) {
            Swal.fire({
                title: MergeFields.translations.merge_fields,
                html: $('<div/>', {style: 'text-align: initial'})
                    .append(MergeFields.modalContent),
                width: 800,
                showCloseButton: true,
                showConfirmButton: false,
                showClass: {
                    popup: 'swal2-noanimation',
                    backdrop: 'swal2-noanimation'
                },
                hideClass: {
                    popup: '',
                    backdrop: ''
                },
                willOpen: function () {
                    var $modal = MergeFields.appendTo(
                        $(Swal.getHtmlContainer()),
                        parameters.show_tickets,
                        parameters.show_canned_responses,
                        parameters.show_organisations
                    );

                    $modal.on('mergefield:inserted', function (e, data) {
                        Swal.close();

                        if (data.value === '') {
                            return;
                        }

                        var instance = codemirror.codemirror(),
                            // Gets the line number in the cursor position
                            cursor = instance.state.selection.main.head,
                            newPosition = cursor + data.value.length;

                        instance.dispatch({changes: {from: cursor, insert: data.value}})
                        setTimeout(function () {
                            instance.focus();
                            instance.dispatch({selection: {anchor: newPosition, head: newPosition}});
                        }, 100);
                    });
                }
            })
        });
    }

    /**
     * Source code editor.
     *
     * @param element
     * @param parameters
     * @constructor
     */
    function Editor(element, parameters)
    {
        "use strict";
        /**
         * Default parameters.
         *
         * @type {object}
         */
        var defaults = {
            toolbar: false,
        };

        var editor,
            toolbar,
            $element = $(element),
            instance = this,
            containerClassName = 'codemirror-box';

        /**
         * Create a container box.
         *
         * @returns {jQuery}
         */
        var createContainer = function () {
            var $container = $('<div/>', {class: containerClassName});
            $element.prev('.cm-editor').wrapAll($container);
        }

        /**
         * Initialise the editor.
         */
        var init = function () {
            // Initialise the text area.
            $element = $(element);
            var options = $.extend(true, defaults, parameters);
            editor = editorFromTextArea(element);

            // Wrap the editor in a container.
            createContainer();

            // Add toolbar if necessary.
            if (options.toolbar) {
                toolbar = new Toolbar(instance.container());
                addToToolbar({
                    toolbar: toolbar,
                    codemirror: instance,
                    show_tickets: parameters.mergefields_tickets,
                    show_organisations: parameters.mergefields_organisations,
                    show_canned_responses: parameters.mergefields_canned_responses,
                    syntaxEmailTemplate: parameters.mergefields_syntaxEmailTemplate
                });
            }
        };

        /**
         * The container element.
         *
         * @returns {jQuery}
         */
        this.container = function () {
            return $element.prev('.' + containerClassName);
        };

        /**
         * The textarea.
         *
         * @returns {*|jQuery|HTMLElement|JQuery<HTMLElement>}
         */
        this.element = function () {
            return $element;
        }

        /**
         * Code mirror instance.
         *
         * @returns {EditorView}
         */
        this.codemirror = function () {
            return editor;
        }

        // Initialise!
        init();
    }

    $.fn.sourcecode = function (options) {
        return $(this).each(function () {
            $(this)[0].sourcecode = new Editor($(this)[0], options);
        });
    };
})($, window, document);
