import {parser as twigParser} from "./syntax.grammar"
import {LRLanguage, LanguageSupport, foldNodeProp} from "@codemirror/language"
import {html, htmlLanguage} from "@codemirror/lang-html"
import {parseMixed} from "@lezer/common"

const twigLanguage = LRLanguage.define({
  parser: twigParser.configure({
    props: [
      foldNodeProp.add({
        IfTag(node) {
          let first = node.firstChild, last = node.lastChild
          if (!first || first.name !== "StartIfTag") return null
          return {from: first.to, to: last.name === "EndIfTag" ? last.from : node.to}
        },
        ForTag(node) {
          let first = node.firstChild, last = node.lastChild
          if (!first || first.name !== "StartForTag") return null
          return {from: first.to, to: last.name === "EndForTag" ? last.from : node.to}
        }
      }),
    ],
    wrap: parseMixed(node => {
      if (node.type.isTop) {
        return {
          parser: htmlLanguage.parser,
          overlay: node => node.type.name === "Text"
        }
      }

      return null;
    })
  }),
  languageData: {
    commentTokens: {block: {open: "{#", close: "#}"}}
  }
})

export function twig() {
  return new LanguageSupport(twigLanguage, [html().support]);
}
